import { RouterModule, Routes } from '@angular/router';

import { DashboardComponent } from './components/playouts/dashboard/dashboard.component';
import { LoginComponent } from './components/playouts/login/login.component';
import { UserComponent } from './components/mains/user/user.component'

const APP_ROUTES: Routes = [
  { path: '', redirectTo: 'dashboards', pathMatch: 'full' },
  { path: 'dashboards', component: DashboardComponent },
  { path: 'login', component: LoginComponent },
  { path: 'user', component: UserComponent },
];
export const Routing = RouterModule.forRoot(APP_ROUTES);



